import React, { Component } from 'react'
import styled from 'styled-components'
import Link from 'gatsby-link'

import particlesConfig from '../json/particles-config.json'

import '../css/particles-styles.css'
import { animateBackground, animateShake } from '../css/animations'

const Header = styled.header`
  ${animateBackground}
  ${animateShake}
  height: 35vh;
  background-color: #ffa81f;
  color: blue;
  position: fixed;
  top: 0;
  right: 0;
  left: 0;
  display: flex;
  justify-content: center;
  align-items: center;
  background: linear-gradient(#ffa81f, #d85d15);
  background-size: 250% 250%;
  animation: animateBg 10s ease infinite;
  font-weight: 400;
  transition: height 250ms ease-in-out;
  user-select: none;
  @media only screen and (min-width: 768px) {
    height: 45vh;
  }
`

const Name = styled.h1`
  display: flex;
  flex-wrap: wrap;
  font-size: 2rem;
  text-transform: uppercase;
  font-weight: 700;
  align-items: center;
  transition: font-size 250ms ease-in-out, padding 150ms ease-in;
  background-color: #002635;
  color: white;
  padding: 0.5rem 1rem;
  margin: 0;
  width: auto;
  user-select: text;
  @media only screen and (min-width: 375px) {
    font-size: 2.5rem;
  }
  @media only screen and (min-width: 768px) {
    font-size: 4.5rem;
    padding: 1rem 2rem;
  }
`

const StyledLink = styled(Link) `
  color: inherit;
`

const First = styled.span `
  padding-right: 2vw;
  font-weight: 700;
  white-space: nowrap;
`

const Letter = styled.span `
  display: inline-block;
  position: relative;
  z-index: 3;
  &:hover {
    animation: shake 1000ms ease-in-out;
  }
`

const Last = styled.span `
  font-weight: 400;
  white-space: nowrap;
`

const NoWrap = styled.span `
  white-space: nowrap;
`

class BlogHeader extends Component {
  componentDidMount () {
    if (window) {
      require.ensure('particles.js', () => {
        this.Particles = require('particles.js')
        this.Particles(`blog-header`, particlesConfig)
      })
    }
  }

  componentWillUnmount () {
    if (this.Particles) {
      this.Particles.destroy()
    }
  }

  render () {
    return (
      <Header id="blog-header" {...this.props}>
        <Name className="name">
          <StyledLink to="/">
            <First>
              {
                'Double'
                  .split('')
                  .map((letter, index) => <Letter key={`${letter}-${index}`}>{letter}</Letter>)
              }
            </First>
            <Last>
              {
                'Sheep'
                  .split('')
                  .map((letter, index) => <Letter key={`${letter}-${index}`}>{letter}</Letter>)
              }
            </Last>
          </StyledLink>
        </Name>
      </Header>
    )
  }
}

export default BlogHeader



